*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.input {
  cursor: pointer;
  width: 600px;
  height: 40px;

  background-color: #fafafa;
  border-radius: 10px;
  box-shadow: 0 0 4px rgba(9, 5, 247, 0.829);
  transition: 0.3s box-shadow;
  border: 0px solid;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.input:hover {
  box-shadow: 0 0 10px rgba(20, 5, 228, 0.5);
}

.input[placeholder] {
  padding-left: 30px;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
}
.btn {
  margin-left: 20px;
  cursor: pointer;
  border-radius: 4px;
  border: 1px solid transparent;
  background: #2196f3;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
}

.btn:hover {
  background: #188ce8;
}
.btn_load {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  padding: 10px 32px;
  border-radius: 4px;
  border: 1px solid transparent;
  background: #2196f3;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  align-items: center;
  cursor: pointer;
}
.btn_load:hover {
  background: #188ce8;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.photo-card {
  outline: 1px solid #188ce8;
  box-shadow: 5px 5px 15px 5px #188ce8;
}

.photo-card img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.stats {
  display: flex;
  justify-content: center;
  align-items: baseline;
}

.stats-item {
  display: flex;
  margin-right: 10px;
}
.stats-item:last-child {
  margin-right: 0px;
}
.stats-text {
  font-weight: bold;
  font-size: 15px;
  color: rgba(145, 17, 34, 0.555);
}
.material-icons {
  display: flex;
  align-items: center;
  margin-right: 10px;
  color: #2196f3;
}
